'use client'

import styles from '@/styles/progress/progress.module.css'
import Image from 'next/image'
import React, { useEffect, useRef } from 'react'
import gsap from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

const Progress = () => {
  const progressRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    if (!progressRef.current) return

    let ctx = gsap.context(() => {
      // 기존 ScrollTrigger 제거 (충돌 방지)
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill())

      // ✅ progressBox 요소 선택
      const sections = gsap.utils.toArray(
        `.${styles.progressBox}`
      ) as HTMLElement[]

      gsap.fromTo(
        sections,
        { opacity: 0, y: 50, scale: 0.95, filter: 'blur(10px)' },
        {
          opacity: 1,
          y: 0,
          scale: 1,
          filter: 'blur(0px)',
          duration: 1.2,
          stagger: 0.2, // ✅ 하나씩 자연스럽게 등장
          ease: 'power3.out',
          scrollTrigger: {
            id: 'progress-trigger',
            trigger: progressRef.current,
            start: 'top 85%', // ✅ 트리거 위치 조정
            end: 'bottom 50%',
            scrub: 0.5, // ✅ 부드러운 스크롤 효과
          },
        }
      )

      ScrollTrigger.refresh()
    }, progressRef)

    return () => ctx.revert() // ✅ GSAP 컨텍스트 정리
  }, [])

  return (
    <section className="sec progress" ref={progressRef}>
      <section className="secInner">
        <div className="titleArea">
          <Image
            src="/images/section/PROGRESS.svg"
            width={693}
            height={158}
            alt="PROGRESS"
          />
          <h2 className="secTitle">これまでの軌跡</h2>
        </div>
        <section className={styles.progressArea}>
          <article className={styles.progressBox}>
            <div className={styles.textarea}>
              <div className={styles.progressYear}>
                <p className="bunyan-bold">2015.4-2016.8</p>
              </div>
              <div className={styles.progressBuilding}>
                <p>帝都渋谷ビル</p>
                <p>間借り5坪</p>
              </div>
            </div>
            <div className={styles.progressbar}></div>
            <div className={styles.imgarea}>
              <figure>
                <Image
                  src="/images/section/progressSection/progress01.png"
                  width={296}
                  height={368}
                  alt="帝都渋谷ビル"
                />
              </figure>
              <div className={styles.imgareaText}>
                <span className="bunyan-regular">Teito Shibuya Bid</span>
                <span></span>
                <span className="bunyan-regular">01</span>
              </div>
            </div>
          </article>
        </section>
      </section>
    </section>
  )
}

export default Progress
